
@font-face {
  font-family: 'shingo';
  font-style: normal;
  font-weight: normal;
  src: url('assets/fonts/medium.woff') format('woff');
}

@font-face {
  font-family: 'shingo';
  font-style: normal;
  font-weight: bold;
  src: url('assets/fonts/bold.woff') format('woff');
}

html {
  margin: 0;
  padding: 0;
  background-color: #24113f;
  font-size: 1rem;
}

@media (max-width: 1000px), (max-height: 800px) {
  html {
    font-size: 0.9rem;
  }
}

@media (max-width: 700px), (max-height: 600px) {
  html {
    font-size: 0.8rem;
  }
}

@media (max-width: 500px), (max-height: 400px) {
  html {
    font-size: 0.7rem;
  }
}

@media (max-width: 400px), (max-height: 300px) {
  html {
    font-size: 0.6rem;
  }
}

input, button, textarea {
  font-family: 'shingo', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
}

body {
  margin: 0;
  font-family: 'shingo', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  color: white;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: transparent;
}

a {
  color: #42c2f5;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
