
.main-menu-buttons {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.large-button {
  font-size: 2rem;
  min-width: 16rem;
  margin-bottom: 2.5rem;
  line-height: 1.8;
  padding: 0.33rem 2.5rem;
  border: none;
  background: transparent;
  border-radius: 0.5rem;
  color: white;
  position: relative;
  cursor: pointer;
}

.large-button:after {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  border: 0.3rem solid #b646a0;
  background: rgba(0, 0, 0, 0.35);
  z-index: -1;
  transform: skew(-15deg);
  border-radius: 0.5rem;
  transition: all 0.1s;
  outline: none;
  box-shadow: 0 0 1.5rem -0.33rem rgba(0, 0, 0, 0.6);
}

.large-button:hover,
.large-button:focus {
  color: white;
  text-shadow: 0 0 2px white;
  outline: none;
}

.large-button:hover:after,
.large-button:focus:after {
  background: rgba(0, 0, 0, 0.6);
  border-color: #2193d1;
  box-shadow: 0 0 1.5rem -0.33rem rgba(0, 0, 0, 1);
}

.large-button:active:after,
.large-button:active:after {
  background: #2193d1;
  border-color: #2193d1;
  box-shadow: 0 0 1.6rem 0 rgba(0, 0, 0, 1);
}