
.App {
  text-align: center;
}

.App-main {
  transition: opacity .28s;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.App-cursor-mask {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 10;
  pointer-events: none;
  background: rgba(0, 0, 0, 0);
  transition: background-color 0.2s;
}

.App-cursor-mask.active {
  cursor: wait;
  pointer-events: all;
  background: rgba(0, 0, 0, 0.1);
}
