
.bg-carousel {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: hidden;
  pointer-events: none;
  z-index: -10;
}

.bg-slide {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  opacity: 0;
  transform: translateX(5%) scale(1.1);
  transition: opacity .3s, transform .3s;
  transition-timing-function: cubic-bezier(0.51, 0.25, 0.86, 0.7);
}

.bg-slide.active {
  opacity: 1;
  transform: translateX(0) scale(1.1);
  transition-timing-function: cubic-bezier(0.16, 0.7, 0.55, 0.92);
}

.bg-slide.outgoing {
  opacity: 0;
  transform: translateX(-5%) scale(1.1);
  transition-duration: 0.2s;
}