
.titled-layout {
  display: flex;
  flex-direction: column;
  margin-top: 10vh;
  height: 90vh;
  align-items: center;
  justify-content: stretch;
}

.titled-layout > *:first-child {
  margin-bottom: 5vh;
}

.titled-layout > img.logo {
  max-width: 70vw;
  max-height: 22vh;
}

.titled-layout__children {
  flex-grow: 0.33;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.titled-layout > img.logo--small {
  max-height: 8vh;
}